import { useDispatch, useSelector } from 'react-redux'
import { Redirect, Link } from 'react-router-dom'
import { ChevronLeft } from 'react-feather'
import { Card, CardBody, CardTitle, CardText, Form, Button } from 'reactstrap'
import '@styles/base/pages/page-auth.scss'

import InputGroup from '@components/input-group'
import UILoader from '@components/ui-loader'
import { forgotPassowrd, forgotPasswordChange } from '@store/ForgotPassword/actions'

const ForgotPassword = (props) => {
  const { authenticated } = useSelector(store => store.authentication),
    { forgotFormData, formErrors, isLoading } = useSelector(store => store.forgotPassword),
    dispatch = useDispatch()

  if (authenticated) return <Redirect to='/dashboard' />

  const handleFormChange = (name, value) => {
    dispatch(forgotPasswordChange(name, value))
  }
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(forgotPassowrd())
  }

  return (
    <UILoader blocking={isLoading}>
      <div className='auth-wrapper auth-v1 px-2'>
        <div className='auth-inner py-2'>
          <Card className='mb-0'>
            <CardBody>
              <Link className='brand-logo' to='/' onClick={e => e.preventDefault()}>
                <h2 className='brand-text text-primary ml-1'>Carhoot</h2>
              </Link>
              <CardTitle tag='h4' className='mb-1'>
                Forgot Password? ðŸ”’
              </CardTitle>
              <CardText className='mb-2'>
                Enter your email and we'll send you instructions to reset your password
              </CardText>
              <Form className='auth-reset-password-form mt-2' onSubmit={handleSubmit}>
                <InputGroup
                  type={'email'}
                  label={'Email Address'}
                  error={formErrors['email']}
                  name={'email'}
                  defaultValue={forgotFormData.email}
                  placeholder={'Please Enter Your Email'}
                  onInputChange={(name, value) => {
                    handleFormChange(name, value)
                  }}
                />
                <Button.Ripple color='primary' block type={'submit'}>
                  Send Email
                </Button.Ripple>
              </Form>
              <p className='text-center mt-2'>
                <Link to='/login'>
                  <ChevronLeft className='mr-25' size={14} />
                  <span className='align-middle'>Back to login</span>
                </Link>
              </p>
            </CardBody>
          </Card>
        </div>
      </div>
    </UILoader>
  )
}

export default ForgotPassword
