/**
 *
 * Input
 *
 */

import React, { useState } from 'react'
import { useForm } from 'react-hook-form'
import { FormGroup, FormText, Input, Label } from 'reactstrap'

const InputGroup = props => {
  const {
    type,
    error,
    step,
    decimals,
    min,
    max,
    disabled,
    placeholder,
    rows,
    label,
    name,
    defaultValue = '',
    onInputChange,
    children,
    required = false,
    autoComplete = 'off',
    ...rest
  } = props

  const [value, setValue] = useState('')
  const [initValue, setInitValue] = useState(false)
  const { register } = useForm()

  const _onChange = e => {
    onInputChange(e.target.name, e.target.value)
    setInitValue(true)
    setValue(e.target.value)
  }

  if (type === 'number') {

    const handleOnInput = e => {
      if (!decimals) {
        e.target.value = e.target.value.replace(/[^0-9]*/g, '')
      }
    }
    return (
      <FormGroup>
        {label && <Label className='form-label'>
          {label} {required && <span className='text-danger'>*</span>}
        </Label>}
        <Input
          autoComplete='on'
          step='step'
          min={min || 0}
          max={max || null}
          pattern='[0-9]'
          onInput={handleOnInput}
          type={type}
          onChange={e => {
            _onChange(e)
          }}
          disabled={disabled}
          name={name}
          value={defaultValue}
          placeholder={placeholder}
          innerRef={register({ required })}
          {...rest}
        />
        <FormText color="danger">{error && error[0]}</FormText>
      </FormGroup>
    )
  } else {
    return (
      <FormGroup>
        {label && <Label className='form-label'>
          {label} {required && <span className='text-danger'>*</span>}
        </Label>}
        <Input
          autoComplete={autoComplete}
          type={type}
          onChange={e => {
            _onChange(e)
          }}
          disabled={disabled}
          name={name}
          value={defaultValue}
          placeholder={placeholder}
          innerRef={register({ required })}
          {...rest}
        >
          {children}
        </Input>
        <FormText color="danger">{error && error[0]}</FormText>
      </FormGroup>
    )
  }
}

InputGroup.defaultProps = {
  step: 1,
  decimals: true,
  rows: '4'
}

export default InputGroup
